@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
/* Roboto Font */

* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
}

body, input, button {
  font-family: Roboto, sans-serif;
}

main {
  width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

h1 {
  font-size: 50px;
}
h2 {
  font-size: 38px;
}
header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  margin: 10px;
}

header a, header img {
  height: 100%; 
  width: auto;
  border-radius: 50%;
}
header img {
  -webkit-filter: invert(1);
          filter: invert(1);
}

header ul {
  list-style: none;
  display: flex;
}
header ul li {
  padding: 10px;
}

@media(max-width: 600px) {
  header {
    justify-content: center;
    display: none;
  }
}
section#About {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  min-height: calc(100vh - 80px);
}

section#About img {
  margin-top: -40px;
  width: 300px;
  height: auto;
  border-radius: 50%;
}

section#About svg {
  width: 25px;
  height: 25px;
  margin: 5px;
}

section#About h1 {
  margin-top: 10px;
}
section#About p {
  color: #555;
  font-size: 20px;
  max-width: 500px;
  text-align: center;
}

section#About ul {
  list-style: none;
  display: flex;
  flex-direction: row;
}
section#Projects {
  padding: 20px;
  background-color: #212121;
  color: white;
}
section#Projects h2 {
  text-align: center;
  margin-bottom: 5px;
}

section#Projects ul {
  list-style: none;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}
ul li.card {
  display: flex;
  flex-direction: column;
  flex: 0 0 auto;
  border-radius: 5px;
  background-color: white;
  color: #212121;
  margin: 5px;
}
li.card a {
  padding: 20px;
}
li.card h3 {
  margin-bottom: 5px;
}
li.card footer {
  display: flex;
  flex-direction: row;
  margin-top: 10px;
}
li.card div.circle {
  background-color: grey;
  width: 18px;
  height: 18px;
  border-radius: 9px;
  margin-right: 5px;
}

@media(max-width: 600px) {
  section#Projects ul {
    grid-template-columns: 1fr;
  }
}
@media(max-width: 900px) {
  section#Projects ul {
    grid-template-columns: 1fr 1fr;
  }
}
div#root > footer {
  background-color: #212121;
  color: white;
  text-align: center;
  padding: 8px;
}
